@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;800&family=Poppins:wght@300;400&family=Ubuntu:wght@300&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{transition:all 300ms ease;box-sizing:border-box}.App{text-align:center;font-family:"Montserrat",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Cantarell,"Open Sans","Helvetica Neue",sans-serif}header .navParent{min-height:10vh;width:100%;display:flex;justify-content:space-around;align-items:center;height:100%;background-color:#528078}header .navParent .logo{font-size:25px;font-weight:300;background-color:#528078;color:white;padding:5px;border:5px solid transparent;position:relative;z-index:10;overflow:hidden}header .navParent .logo::before{content:"";position:absolute;top:0;left:-90px;width:120%;height:40%;background-color:#eee;-webkit-filter:blur(10px);filter:blur(10px);transform:rotate(-45deg);-webkit-animation:logoBefore 2s ease-out infinite;animation:logoBefore 2s ease-out infinite}header .navParent .logo::after{content:"";position:absolute;top:0;left:-80px;width:100%;height:20%;background-color:#eee;-webkit-filter:blur(15px);filter:blur(15px);transform:rotate(-45deg);-webkit-animation:logoBefore 2s ease-out 0.1s infinite;animation:logoBefore 2s ease-out 0.1s infinite}header .navParent .navigation ul{list-style:none;margin:0;padding:0}header .navParent .navigation ul li{float:left}header .navParent .navigation ul li button{font-size:14px;font-weight:600;display:block;height:100%;background-color:#528078;color:white;padding:14px 16px;border:none;outline:none;box-shadow:none}header .navParent .navigation ul li button:hover{color:#bad445;background-color:#3e615b;cursor:pointer}main{width:100%;background-image:url(/landingpage/static/media/kat.0077091f.jpg);height:500px;background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover}footer{background-color:#528078}footer .footerContainer{display:flex;flex-direction:row;justify-content:space-around;align-items:center;padding:10px 0}@media (min-width: 300px) and (max-width: 768px){footer .footerContainer{flex-direction:column}}footer .footerContainer .footerChild{width:30%;background-color:#3e615b;padding:10px}footer .footerContainer .footerChild strong{color:#528078;padding:10px;background-color:#cfcece;border:1px solid white}footer .footerContainer .footerChild strong:hover{color:white;border:1px solid white;background-color:rgba(255,255,255,0.205);cursor:pointer}@media (min-width: 300px) and (max-width: 768px){footer .footerContainer .footerChild{margin:20px 0;width:80%}}@-webkit-keyframes logoBefore{0%{left:-90}50%{left:100px}100%{left:200px}}@keyframes logoBefore{0%{left:-90}50%{left:100px}100%{left:200px}}

